.hystmodal {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    overflow: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    opacity: 1;
    min-width: 0;
    pointer-events: none;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    z-index: 99;
    visibility: hidden;
}
.hystmodal--active{
    opacity: 1;
}
.hystmodal--moved,
.hystmodal--active{
    pointer-events: auto;
    visibility: visible;
}
.hystmodal__wrap {
    flex-shrink: 1;
    flex-grow: 0;
    width: 100%;
    min-width: 0;
    height: 100%;
    margin: auto;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
}
.hystmodal__window {
    margin:0 auto;
    box-sizing: border-box;
    flex-shrink: 0;
    flex-grow: 0;
    width: 92%;
    aspect-ratio: 1.8;
    overflow: visible;
    transition: transform 0.2s ease 0s, opacity 0.2s ease 0s;
    transform: scale(0.9);
    opacity: 0;
    @media screen and (max-width: $smTabletWidth){
        width: 95%;
        height: 60%;
  }
}
.hystmodal--active .hystmodal__window{
    transform: scale(1);
    opacity: 1;
}
.hystmodal__close{
    position: absolute;
    z-index: 10;
    top:40px;
    right: 40px;
    display: block;
    width: 30px;
    height: 30px;
    background-color: transparent;
    background-position: center center;
    background-repeat: no-repeat;
    background-image: url(../img/svg/close.svg);
    background-size: 100% 100%;
    border: none;
    font-size: 0;
    cursor: pointer;
    outline: none;
    @media screen and (max-width: $mediumWidth ) {
        width: 20px;
        height: 20px;
    }
}
.hystmodal__close:focus{
    outline: 2px dotted #afb3b9;
    outline-offset: 2px;
}
@media all and (max-width:767px){
    .hystmodal__close{
        top:10px;
        right: 10px;
        width: 24px;
        height: 24px;
        background-image: url(../img/svg/close.svg);
    }
    .hystmodal__window{
        margin: 0;
    }
}